.linkStyle{
    background-color: rgb(74, 143, 6);
    margin: 0 30px 0 30px;
    padding: 0 5px 0 5px;
    font-size: 15px;
    border-color: darkgreen;
    border-radius: 5px;
    -webkit-transition: 0.8s;
    transition: 0.8s;
    cursor: pointer;
    color: azure;
    width: 100%;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
} 

.linkStyle:hover{
    color: rgb(207, 36, 70);
    -webkit-transform: scaleX(0.9);
            transform: scaleX(0.9);
    background-color: rgb(36, 54, 4);
    
}


