.btnSelect{
    width: 100%;
    border: 2px solid #4caf50;
    color: black;
  padding: 5px;
  font-size: 12px;
  cursor: pointer;
}

.btnSelect:hover{
    background-color: yellowgreen;
    color: blue;
    transition: ease-in 0.5s;
}

.select-box{
    width: 100%;
    background-color: yellowgreen;
    text-align: center;
    object-position: center;
    font-size: 12px;
    font-family: sans-serif;
    height: 30px;

}



#answer{
    font-weight: bold;
}

#tableTools{
    border: solid;
    border-color: rgb(42, 140, 165);
    display: inline-block;
}

.square-room{
    border:solid;
    border-radius: 5px;
    position: relative;
    top:-200px
}