@import "https://use.fontawesome.com/releases/v5.5.0/css/all.css";
.container{
    /* width: 100%;
  margin: 0;
  padding: 0; */
  font-family: sans-serif;
  background: url('room2.jpg') no-repeat;
  background-size: cover;
  background-position-y: center;
  align-items:center;
    justify-content:center;
    object-position: center;
    height: 100%;
}
.login-box{
    align-items:center;
    justify-content:center;
    object-position: center;
  width: 280px;
  /* position: absolute;
  top: 50%;
  left: 50%; */
  /* transform: translate(-10%,-10%); */
  color: white;
  display: inline-block;
  background-color: grey;
  opacity: 0.7;
}
.login-box h1{
  float: left;
  font-size: 30px;
  border-bottom: 6px solid #38b618;
  margin-bottom: 20px;
  padding: 13px 0;
  
  
}

.textbox{
  width: 100%;
  overflow: hidden;
  font-size: 20px;
  padding: 8px 0;
  margin: 8px 0;
  border-bottom: 1px solid #4caf50;
}
.textbox i{
  width: 22px;
  float: left;
  text-align: center;
  
  
  
  
}
.textbox input{
  border: none;
  outline: none;
  background: none;
  color: white;
  font-size: 18px;
  width: 80%;
  float: left;
  margin: 0 10px;
  z-index: 2;
  font:white;
  
}
::-webkit-input-placeholder{
    color: white;
    opacity: 0.7;
}
.textbox:hover{
    background-color: yellowgreen;
    transition: ease-in 0.5s;
}
.btn{
  width: 100%;
  background: none;
  border: 2px solid #4caf50;
  color: white;
  padding: 5px;
  font-size: 18px;
  cursor: pointer;
  margin: 12px 0;
}
.btn:hover{
    background-color: yellowgreen;
    transition: ease-in 0.5s;
}